<template>
	<div class="logo" />
	<a-menu theme="dark" mode="inline" v-model:selectedKeys="selectedMenuKeys" @click="handleMenuClick">
		<a-menu-item key="Dashboard">
			<AppstoreOutlined />
			<span>Home</span>
		</a-menu-item>
		<a-menu-item key="Goods-List">
			<ShopOutlined />
			<span>Warehouse</span>
		</a-menu-item>
		<a-menu-item key="ImportNote-List">
			<ShoppingCartOutlined />
			<span>Import Note</span>
		</a-menu-item>
		<a-menu-item key="ExportNote-List">
			<FormOutlined />
			<span>Export Note</span>
		</a-menu-item>
		<a-menu-item key="Provider">
			<ApartmentOutlined />
			<span>Provider</span>
		</a-menu-item>
		<a-menu-item key="Customer">
