<script>
import { ref } from 'vue'
import {
	AppstoreOutlined,
	ShopOutlined,
	ShoppingCartOutlined,
	FormOutlined,
	ApartmentOutlined,
	TeamOutlined,
	AreaChartOutlined,
	SettingFilled,
} from '@ant-design/icons-vue'
import { useRouter } from 'vue-router'

export default {
	components: {
		AppstoreOutlined,
