		FormOutlined,
		ApartmentOutlined,
		TeamOutlined,
		AreaChartOutlined,
		SettingFilled,
	},
	setup(props, context) {
		const router = useRouter()
		const handleMenuClick = menu => {
			router.push({ name: menu.key, params: {} })
			context.emit('handleShowDrawer', false)
		}
		const selectedMenuKeys = ref([''])
		return { handleMenuClick, selectedMenuKeys }
	},
}
</script>

